export function logic(res, callback) {
    if (res.data.status === 1) {
        callback(res.data.result);
    } else if (res.data.status === 0) {
        console.log(res.data.message);
    } else {
        console.log('Áä∂ÊÄÅÁ†ÅÈîôËØØ');
    }
}


export function getPosition(callback) {
    // eslint-disable-next-line no-undef
    let map = new AMap.Map('iCenter');
    map.plugin('AMap.Geolocation', function () {
        // eslint-disable-next-line no-undef
        let geolocation = new AMap.Geolocation({
            enableHighAccuracy: true,//ÊòØÂê¶‰ΩøÁî®È´òÁ≤æÂ∫¶ÂÆö‰ΩçÔºåÈªòËÆ§:true
            timeout: 10000,          //Ë∂ÖËøá10ÁßíÂêéÂÅúÊ≠¢ÂÆö‰ΩçÔºåÈªòËÆ§ÔºöÊó†Á©∑Â§ß
            maximumAge: 0,           //ÂÆö‰ΩçÁªìÊûúÁºìÂ≠ò0ÊØ´ÁßíÔºåÈªòËÆ§Ôºö0
            convert: true,           //Ëá™Âä®ÂÅèÁßªÂùêÊ†áÔºåÂÅèÁßªÂêéÁöÑÂùêÊ†á‰∏∫È´òÂæ∑ÂùêÊ†áÔºåÈªòËÆ§Ôºötrue
        });
        geolocation.getCurrentPosition(function (status, result) {
            if (status === 'complete') {
                let position = {
                    longitude: result.position.lng,
                    latitude: result.position.lat,
                    city: result.addressComponent.city,
                };
                callback(position);
            } else if (status === 'error') {
                if (result.info['NOT_SUPPORTED']) {
                    alert('ÂΩìÂâçÊµèËßàÂô®‰∏çÊîØÊåÅÂÆö‰ΩçÂäüËÉΩ ');
                }
                if (result.info['FAILED']) {
                    alert(result.message);
                }
                return {
                    longitude: 39.90881451099204,
                    latitude: 116.39774322510002,
                    city: 'Âåó‰∫¨Â∏Ç',
                };
            } else {
                alert('Êú™Áü•ÈîôËØØ');
            }
        });
    });
}